@import '../../../style/variables.scss';

.chart__money{
    cursor: pointer;
    width: 500px;
    max-height: 300px;
    padding: 20px;
    background: $white-color;
    box-shadow: 0px 12px 26px rgba(16, 30, 115, 0.06);
    border-radius: 8px;
    @media (max-width: 375px) {
        max-width: 320px;
    }
}